<template>
  <div>
    <h1>{{ title }}</h1>
    <label for="birth">{{msg}}</label>
    <input id="birth" type="date" v-model="birthday" name="user_date" />
    <p/>
    <button class="btn" @click='convert'>计算</button>
    <p class="hint">本站点承诺不会记录用户任何数据</p>
</div>
</template>

<script>
export default {
    name:"EEC",
    props: {
        msg: String,
    },
    data() {
        return {
            birthday: null,
            title: '距离高考还有?天'
        }
    },
    mounted(){
        
    },
    methods: {
        convert: function(){
            // var date1=new Date('2037,6,7')
            var date1 = new Date(this.birthday)
            if(date1.getMonth > 8){// 晚一年上学
                date1.setFullYear(date1.getFullYear() + 19);
            }else{
                date1.setFullYear(date1.getFullYear() + 18);
            }
            date1.setMonth(5);
            date1.setDate(7);
            var date2 = new Date()
            date2.getTime()-date1.getTime()
            var date3 = date1.getTime()-date2.getTime()
            // eslint-disable-next-line
            console.log(date1)
            var days = Math.floor(date3/(24*3600*1000))
            this.title = '距离高考还有'+days+'天'
            return days
        }
    },
}
</script>

<style scoped>
    .btn{
        background-color: #3385ff;
        color: #fff;
        /* border-radius: 20%; */
        width: 100px;
        height: 40px;
    }
    .hint{
        position: absolute;
        bottom: 0;
        width: 100%;
    }
</style>